import React, { createContext, useState, useEffect, useContext } from "react";
import api from "@/lib/axios";
import { useRouter } from "next/navigation";
import Cookies from "js-cookie"; // Import js-cookie

const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [logoutTimer, setLogoutTimer] = useState(null);
  const router = useRouter();

  useEffect(() => {
    const fetchUser = async () => {
      try {
        const token = Cookies.get("auth_token"); // Get token from cookies
        if (token) {
          api.defaults.headers.common["Authorization"] = `Bearer ${token}`;
          const response = await api.get("/user");
          setUser(response.data);
        }
      } catch (error) {
        console.error("Failed to fetch user data:", error.response?.data || error.message);
      } finally {
        setIsLoading(false);
      }
    };

    fetchUser();
  }, []);

  const login = async (credentials) => {
    try {
      const response = await api.post("/login", credentials);
      const { token, user } = response.data;
      Cookies.set("auth_token", token, { expires: 2 }); // Store token in cookies with 2 hours expiry
      api.defaults.headers.common["Authorization"] = `Bearer ${token}`;
      setUser(user);
      scheduleAutoLogout(2 * 60 * 60 * 1000); // 2 hours
      router.push("/dashboard");
    } catch (error) {
      console.error("Login failed:", error.response?.data || error.message);
      throw error;
    }
  };

  const logout = async () => {
    try {
      await api.post("/logout");
      Cookies.remove("auth_token"); // Remove token from cookies
      delete api.defaults.headers.common["Authorization"];
      setUser(null);
      clearAutoLogout();
      router.push("/"); // Redirect to login
    } catch (error) {
      console.error("Error logging out:", error.response?.data || error.message);
    }
  };

  const scheduleAutoLogout = (timeout) => {
    clearAutoLogout();
    const timer = setTimeout(() => {
      logout();
    }, timeout);
    setLogoutTimer(timer);
  };

  const clearAutoLogout = () => {
    if (logoutTimer) {
      clearTimeout(logoutTimer);
      setLogoutTimer(null);
    }
  };

  useEffect(() => {
    return () => clearAutoLogout();
  }, []);

  return (
    <AuthContext.Provider value={{ user, isLoading, login, logout }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => useContext(AuthContext);
